.chat-message-group {
    margin-bottom: 1rem;
    &__meta {
        margin: 0 0 0.3rem 0;
        &__name {
            font-weight: bold;
        }
        &__timestamp {
            font-style: italic;
            color: lightgray;
            font-size: 0.5rem;
        }
    }
}

.chat-message {
    margin: 0;
}