.timer {
    display: grid;
    grid-template-columns: 1fr auto auto auto;
    justify-content: end;
    font-size: 1rem;

    &__legend {
        justify-self: end;
        align-self: center;
        margin: 0 0.5rem 0 0;
    }
    &__devider {
        font-size: 1.2rem;
        padding: 0.5rem 0.2rem;
        margin: 0;
    }
    &__segment {
        width: 2.5rem;
        background-color: lightgray;
        font-size: 1.2rem;
        padding: 0.5rem 0;
        margin: 0;
        text-align: center;
    }
}
