.chat {
    overflow-y: auto;

    border: 1px solid lightgray;
    border-radius: 5px;

    display: grid;
    grid-template-rows: auto 5rem;

    &__history {
        margin: 1rem;
        word-break: break-word;
        overflow-y: auto;
    }

    &__input {
        margin: 1rem;

        & > * {
            width: 100%;
            height: 100%;
            max-width: unset;
            min-width: unset;
            max-height: unset;
            min-height: unset;
            padding: 0;
        }
    }
}