.client {
    height: 40vw; // 54vw;
    max-width: 1440px;
    max-height: 900px;
    margin: auto;

    display: grid;
    grid-template-columns: 3fr 1fr;
    grid-template-rows: auto;
    grid-template-areas: "video side";
    // padding: 70px 50px;
    // background-color: white;

    & main {
        grid-area: "video";
        height: 40vw; // 54vw;
        max-height: 900px;

        display: grid;
        grid-template-columns: 2rem auto 15rem 2rem;
        grid-template-rows: auto 10rem 1.5rem 1.5rem 2.5rem 1.5rem;
        margin-right: 7rem;
        box-shadow: 0px 0px 10px 0px gray;
        border-radius: 0.2rem;
    
        & .remote__mute {
            justify-self: start;
            grid-column: 2 / span 1;
            grid-row: 5 / span 1;
    
            &--deafen {
                &:before {
                    content: 'D';
                    color: lightgray;
                }
            }
            
            &--undeafen {
                &:before {
                    content: 'U';
                    color: lightgray;
                }
            }
        }
        & .remote__video {
            background-color: lightblue;
            grid-column: 1 / span 4;
            grid-row: 1 / span 3;
            margin: 0.1rem;
            position: relative;
            overflow: hidden;

            & video {
                position: absolute;
                left: 0;
                top: 0;
                min-width: 100%;
                min-height: 100%;
                width: auto;
                height: auto;
                background-size: cover;
                overflow: hidden;
            }
        }
        & .local {
            z-index: 1;
            grid-column: 3 / span 1;
            grid-row: 2 / span 1;
            display: grid;
            grid-template-columns: 3rem auto;
            grid-template-rows: 1fr 1fr;
        }
        & .timer {
            grid-column: 3 / span 1;
            grid-row: 5 / span 1;
        }
    }
    & aside {
        grid-area: "side";
        height: 40vw; // 54vw;
        max-height: 900px;
    }

    button {
        &.button--icon {
            border: none;
            font-size: 1.5rem;
            width: 1.5rem;
            height: 1.5rem;
            padding: 0;
            background-color: transparent;
            color: transparent;
            cursor: pointer;
            overflow: hidden;
            outline: none;
        }
    }
}
