.toolbar {
    display: grid;
    grid-template-rows: 4rem auto 5.5rem;
    font-size: 1rem; 

    &.toolbar--switchable {
        grid-template-rows: 4rem auto 5rem 5.5rem;

        > section {
            border-bottom: 1px lightgray solid;
            overflow-y: auto;
            > h1 {
                font-size: 1rem;
                margin: 0;
            }
            > p {
                margin: 0.5rem 0 1.5rem;
            }

            &.upDown {
                > p {
                    min-height: 30%;
                }
            }

            > ol {
                margin: 0;
                padding-inline-start: 1.2rem;

                > li {
                    margin-bottom: 1rem;
                }
            }
        }

        > button {
            justify-self: center;
            margin-top: 1rem;

            &:before {
                content: 'X';
                color: lightgray;
            }
        }
    }

    > nav {
        color: lightgray;
        font-variant: all-petite-caps;
        font-weight: bold;
        width: 100%;
        font-size: 1.3rem;
        padding: 0.1rem 0 1rem;

        > ol {
            display: grid;
            grid-template-columns: auto auto auto;
            justify-content: space-between;
            padding: 0;
            margin: 0;

            > li {
                justify-self: center;
                cursor: pointer;
                list-style: none;

                &:first-child {
                    justify-self: start;
                }
                &:last-child {
                    justify-self: end;
                }

                &.active {
                    color: gray;
                }
            }
        }
    }

    > section {
        > p {
            margin: 0.5rem 0 1.5rem;
        }
    }
}