.local {
    background-color: white;
    margin: 0.1rem;
    border-radius: 0.2rem;

    &__visibility {
        grid-column: 1 / span 1;
        grid-row: 1 / span 1;
        justify-self: center;
        align-self: center;
        
        &--pause {
            &:before {
                content: 'P';
                color: lightgray;
            }
        }

        &--resume {
            &:before {
                content: 'R';
                color: lightgray;
            }
        }
    }

    &__mute {
        grid-column: 1 / span 1;
        grid-row: 2 / span 1;
        justify-self: center;
        align-self: center;
        
        &--unmute {
            &:before {
                content: 'U';
                color: lightgray;
            }
        }

        &--mute {
            &:before {
                content: 'M';
                color: lightgray;
            }
        }
    }

    &__video {
        grid-column: 2 / span 1;
        grid-row: 1 / span 2;

        width: 98%;
        height: 98%;
        margin: 0.1rem;
        position: relative;
        overflow: hidden;

        & video {
            position: absolute;
            left: 0;
            top: 0;
            min-width: 100%;
            min-height: 100%;
            width: auto;
            height: 100%;
            background-size: cover;
            overflow: hidden;
        }
    }
}
